<template>
  <div class="camera-modal">
    <div v-if="posting">Posting...</div>
    <div v-if="error">Couldn't add the post, try again later</div>
    <video ref="video"
           class="camera-stream" />
    <div class="camera-modal-container">
      <span @click="capture"
            class="take-picture-button take-picture-button mdl-button mdl-js-button mdl-button--fab mdl-button--colored">
        <i class="material-icons">camera</i>
      </span>
    </div>
  </div>
</template>

<script>
import postCat from "@/mixins/postCat";

export default {
  data() {
    return {
      mediaStream: null,
      posting: false,
      error: false
    };
  },
  mixins: [postCat],
  mounted() {
    navigator.mediaDevices
      .getUserMedia({ video: true })
      .then(mediaStream => {
        this.mediaStream = mediaStream;
        this.$refs.video.srcObject = mediaStream;
        this.$refs.video.play();
      })
      .catch(error => console.error("getUserMedia() error:", error));
  },
  methods: {
    capture() {
      this.error = false;
      this.posting = true;
      const mediaStreamTrack = this.mediaStream.getVideoTracks()[0];
      const imageCapture = new window.ImageCapture(mediaStreamTrack);
      const imageName = `picture-${new Date().getTime()}`;
      return imageCapture.takePhoto().then(blob => {
        this.uploadImage(blob, imageName)
          .then(url => {
            return this.postCat(url, "Hello");
          })
          .then(() => {
            this.posting = false;
            this.$router.push("/");
          })
          .catch(err => {
            console.error("Camera: ", err);
            this.posting = false;
            this.error = true;
          });
      });
    }
  },
  destroyed() {
    const tracks = this.mediaStream.getTracks();
    tracks.map(track => track.stop());
  }
};
</script>

<style scoped>
.camera-modal {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background-color: white;
  z-index: 10;
}
.camera-stream {
  width: 100%;
  max-height: 100%;
}
.camera-modal-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  align-items: center;
  margin-bottom: 24px;
}
.take-picture-button {
  display: flex;
}
</style>
